* {
  box-sizing: border-box; }

body {
  font-family: Helvetica;
  font-size: 16px; }

button {
  display: block;
  border: none;
  background: none; }

.button {
  display: block;
  cursor: pointer;
  border: none;
  border-radius: 3px;
  outline: none;
  background: transparent; }

.modal-overlay {
  position: fixed;
  z-index: 9;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 3rem;
  transition-timing-function: ease-out;
  transition-duration: 0ms;
  touch-action: none; }
  @media screen and (max-width: 768px) {
    .modal-overlay {
      padding: 0; } }

.modal-shadow {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5); }

.cross {
  position: absolute;
  z-index: 9;
  top: 0;
  right: 0;
  width: 3rem;
  height: 3rem;
  cursor: pointer;
  transition: 0.1s ease-out;
  text-align: center;
  opacity: 0.6;
  color: #fff;
  font-size: 3rem; }
  @media screen and (max-width: 768px) {
    .cross {
      top: 0.5rem;
      right: 0.5rem;
      width: 2rem;
      height: 2rem;
      padding: 0.5rem;
      color: #111;
      border-radius: 50%;
      background-color: #fff;
      font-size: 1rem; } }
  .cross::after {
    content: '\2715'; }
  .cross:hover {
    opacity: 1; }

.modal-body {
  width: 100%;
  height: 100%;
  background-color: #111; }

.body--modal-is-opened {
  overflow: hidden;
  height: 100%; }

.images {
  display: flex;
  overflow-y: auto;
  flex-direction: row;
  flex-wrap: wrap;
  padding-bottom: 5px;
  padding-left: 5px; }
  .images::after {
    flex-grow: 9999;
    content: ''; }

.preview {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  outline: none; }
  .preview__track {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%; }
  .preview__image {
    max-width: 100%;
    max-height: 100%; }
  .preview__image-wrapper {
    right: 100%;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center; }
  .preview__image-wrapper_prev, .preview__image-wrapper_next {
    position: absolute; }
  .preview__image-wrapper_prev {
    right: 100%; }
  .preview__image-wrapper_next {
    left: 100%; }
  .preview__image_prev, .preview__image_next {
    max-width: 100%;
    max-height: 100%; }
  .preview:hover .preview__control {
    opacity: 0.5; }
  .preview__control {
    position: absolute;
    z-index: 1;
    top: 0;
    width: 100px;
    height: 100%;
    padding: 0;
    cursor: pointer;
    transition: 0.2s ease-out;
    opacity: 0;
    outline: none; }
    @media screen and (max-width: 768px) {
      .preview__control {
        display: none; } }
    @media (min-width: 768px) {
      .preview__control {
        display: block;
        opacity: 0.5; } }
    .preview__control:hover {
      opacity: 1 !important; }
    .preview__control::after {
      position: absolute;
      display: block;
      width: 15px;
      height: 15px;
      content: '';
      border: 1px solid #fff;
      border-bottom-color: transparent;
      border-left-color: transparent; }
    .preview__control_prev {
      left: 0;
      background: linear-gradient(-90deg, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.7)); }
      .preview__control_prev::after {
        top: 50%;
        left: 50%;
        transform: translate3d(-30%, -50%, 0) rotate(-135deg); }
    .preview__control_next {
      right: 0;
      background: linear-gradient(90deg, rgba(51, 51, 51, 0), rgba(51, 51, 51, 0.7)); }
      .preview__control_next::after {
        top: 50%;
        right: 50%;
        transform: translate3d(30%, -50%, 0) rotate(45deg); }

.image-tile {
  position: relative;
  overflow: hidden;
  flex-grow: 1;
  height: 200px;
  margin: 5px;
  margin-bottom: 0;
  margin-left: 0;
  cursor: pointer;
  outline: none; }
  .image-tile::after {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    content: '';
    transition: 0.1s ease-out;
    opacity: 0;
    background-color: #000; }
  @media not all and (pointer: coarse) {
    .image-tile:hover::after, .image-tile:focus::after {
      visibility: visible;
      opacity: 0.3; } }
  .image-tile__image {
    flex-grow: 1;
    min-width: 100%;
    max-width: 100%;
    height: inherit;
    object-fit: cover; }
  .image-tile__description {
    font-size: 0.9rem;
    line-height: 1.1rem;
    padding: 5px;
    color: #fff;
    width: 100%;
    position: absolute;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 1;
    opacity: 0;
    transform: translate3d(0, 100%, 0);
    transform-origin: 100% 100%;
    transition: 0.1s ease-out; }
  .image-tile:hover .image-tile__description, .image-tile:focus .image-tile__description {
    opacity: 1;
    transform: translate3d(0, 0, 0); }
  @media (pointer: coarse) {
    .image-tile .image-tile__description {
      opacity: 1;
      transform: translate3d(0, 0, 0); } }
  .image-tile__link {
    color: #bbb; }
    .image-tile__link:hover {
      text-decoration: none; }

@keyframes spin {
  from {
    transform: rotateZ(0); }
  to {
    transform: rotateZ(360deg); } }

.loading-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 200px;
  max-height: 100%; }

.spinner {
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  border: 2px solid #555;
  border-top-color: transparent;
  border-radius: 50%; }

.load-more {
  display: flex;
  align-self: center;
  flex-basis: fill;
  height: 2rem;
  text-align: center;
  background-color: #77d4ff;
  font-size: 1rem;
  line-height: 2rem; }

.to-top {
  position: fixed;
  right: 20px;
  bottom: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64px;
  height: 64px;
  transition: 0.2s ease-in-out;
  transform: scale(0);
  opacity: 0;
  border-radius: 50%;
  background-color: #000; }
  .to-top:hover {
    opacity: 1; }
  .to-top::after {
    display: block;
    width: 0;
    height: 0;
    content: '';
    transition: 0.2s ease-out;
    transform: translate3d(0, 25%, 0) rotateZ(45deg);
    border: 5px solid #fff;
    border-right-color: transparent;
    border-bottom-color: transparent; }

.to-top--showed {
  transform: scale(1);
  opacity: 0.8; }

.to-top--to-bottom::after {
  transform: rotateZ(225deg); }



